import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import { Button,
  Header,
  Grid, Container, Image } from 'semantic-ui-react';
import image from '../../assets/images/white-image.png'

export default class Index extends React.Component {
  render() {
    return <div id='rhinoplasty' className='procedure-page'>
             <section className='hero'>
               <Container>
                 <Header as='h1' className='heading'>Orrplasztika</Header>
                 <Header as='h1'>Megbeszéljük, megtervezzük, megcsináljuk. Együtt.</Header>
               </Container>
             </section>
             <section className='description'>
               <Container>
                 <Grid stackable columns={ 2 }>
                   <Grid.Column className='text'>
                     <p>
                       Az arc karakterét meghatározza az orr nagysága és formája, jelentőségét fokozza, hogy öltözködéssel, sminkkel, egyébbel nem korrigálható megjelenése. A nem kívánatos
                       orrforma lehet öröklött, de nagyon gyakran nem is emlékezetes gyermekkori sérülés, fejlődési rendellenesség , esetleg orrcsonttörés torzítja. Nagyon
                       gyakori rendellenesség az orrsövény ferdülés, mely szintén szerepet játszhat az orrforma kialakulásában, általában orrlégzési nehezítettséget, esetleg
                       horkolást is okoz.
                     </p>
                   </Grid.Column>
                   <Grid.Column>
                     <div className='content'>
                       <Image src={ image }></Image>
                     </div>
                   </Grid.Column>
                   <Grid.Column>
                     <div className='content'>
                       <Image src={ image }></Image>
                     </div>
                   </Grid.Column>
                   <Grid.Column className='text'>
                     <p>
                       A paciens számára legmegfelelőbb technika a konzultáció alkalmával kerül meghatározásra. Az orrlégzés helyreállítás és az alaki változtatás egy műtéttel, altatásban
                       történik. A hegek általában az orron belül, vagy alig láthatóan helyezkednek el. A műtétet követően egy éjszakát a paciens az intézetben tölt orvosi
                       felügyelet alatt, gipszsín viselése 5-8 nap. Mivel orrtampont nem használok, ezért a tampon eltávolításától nem kell tartani. Várhatóan a szem körüli
                       duzzanat 10-15nap alatt felszívódik, “szalonképes” megjelenés a 3. héttől várható. Fizikai terhelés 6 hétig kerülendő. A végleges forma 6-18 hónap elteltével
                       alakul ki, egyéntől függően.
                     </p>
                   </Grid.Column>
                 </Grid>
                 <div className='button-container'>
                   <Button secondary>Orrplasztika galéria</Button>
                 </div>
               </Container>
             </section>
             <section className='price-info'>
               <Container>
                 <Grid stackable columns={ 2 }>
                   <Grid.Column>
                     <Header as='h2'>Részleges orrplasztika</Header>
                   </Grid.Column>
                   <Grid.Column>
                     <Header as='h2' className='price'>320 000 – 480 000 Ft</Header>
                   </Grid.Column>
                   <Grid.Column>
                     <Header as='h2'>Teljes orrplasztika altatásban</Header>
                   </Grid.Column>
                   <Grid.Column>
                     <Header as='h2' className='price'>690 000 – 790 000 Ft</Header>
                   </Grid.Column>
                   <Grid.Column>
                     <Header as='h2'>Másodlagos orrplasztika</Header>
                   </Grid.Column>
                   <Grid.Column>
                     <Header as='h2' className='price'>790 000 Ft-tól</Header>
                   </Grid.Column>
                 </Grid>
               </Container>
             </section>
             <section className='cta'>
               <Button primary>Időpontot kérek</Button>
             </section>
           </div>
  }
}



